.footer {
  background-color: #1a1a1a;
  color: var(--primary-color);
  padding: 3rem 1rem 2rem;
  font-family: 'Inter', sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.footer-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.footer-main {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 2rem;
}

.footer-section {
  flex: 1;
  min-width: 250px;
  text-align: left;
}

.footer-logo {
  font-size: 1.75rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--primary-color);
  text-align: left;
}

.footer-description {
  font-size: 0.95rem;
  color: var(--gray-300);
  margin-bottom: 1rem;
}

.footer-social {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.social-link {
  
  background-color: #1a1a1a;
  color:var(--gray-300);
  border-radius: 10px;
  padding: 0.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: transform 0.2s ease;
  width: 80px;
  height: 44px;
  min-width: 44px;
  min-height: 44px;
  flex-shrink: 0;
}

.social-link svg {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.footer-nav {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.footer-link {
  background: none;
  border: none;
  color: var(--gray-300);
  font-size: 1rem;
  text-align: left;
  cursor: pointer;
  transition: color 0.2s ease;
}

.footer-link:hover {
  color: var(--primary-color);
}

.footer-bottom {
  border-top: 1px solid #333;
  padding-top: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.footer-copyright {
  font-size: 0.875rem;
  color: var(--gray-300);
}

.heart {
  color: var(--primary-dark);
  display: inline-flex;
  align-items: center;
  margin: 0 0.2rem;
}

.back-to-top {
  background-color: var(--primary-color);
  border: none;
  border-radius: 9999px;
  color: white;
  padding: 0.6rem;
  cursor: pointer;
  transition: transform 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.back-to-top svg {
  width: 16px;
  height: 16px;
}

@media (max-width: 768px) {
  .footer-main {
    flex-direction: column;
    align-items: flex-start;
  }

  .footer-logo {
    text-align: left;
    width: 100%;
  }

  .footer-section {
    text-align: left;
    width: 100%;
  }

  .footer-social {
    justify-content: flex-start;
  }

  .footer-nav {
    align-items: flex-start;
  }
}

@media (max-width: 400px) {
  .social-link {
    width: 40px;
    height: 40px;
    padding: 0.4rem;
  }

  .social-link svg {
    width: 18px;
    height: 18px;
  }
}
